import React, { Component } from "react";

class Resume extends Component {
  render() {
    if (this.props.data) {
      //var semestermessage = this.props.data.semestermessage;
      var topicmessage = this.props.data.topicmessage;
      var education = this.props.data.education.map(function (education) {
        return (
          <div key={education.school}>
            <h3>{education.school}</h3>
            <p className="info">
              {education.degree} <span>&bull;</span>
              <em className="date">{education.graduated}</em>{" "}
              <span>&bull;</span>
              {education.ECTSPoint}
            </p>
            <p>{education.description}</p>
          </div>
        );
      });
      var work = this.props.data.work.map(function (work) {
        return (
          <div key={work.company}>
            <h3>{work.company}</h3>
            <p className="info">
              {work.title}
              <span>&bull;</span> <em className="date">{work.years}</em>
            </p>
            <p>{work.description}</p>
          </div>
        );
      });

      var mobile = this.props.data.Mobile.map(function (Mobile) {
        return (
          <div key={Mobile.title}>
            <h3>{Mobile.title}</h3>
            <p className="info">
             Available on <span>&bull;</span> <em className="date">{Mobile.available}</em>
            </p>
            <p>{Mobile.description}</p>
          </div>
        );
      });

      // All Semesters
      var skills = this.props.data.Semesters.map(function (skills) {
        var className = "bar-expand " + skills.name.toLowerCase();
        /*
        return (
          <li key={skills.name}>
            <span
              style={{ width: skills.progress }}
              className={className}
              label={`${skills.ECTS} ECTS`}
            ></span>
            <em>
              {skills.name} &bull; {skills.ECTS} ECTS
            </em>
          </li>
        );
        */
      });

      // Programming
      var topics = this.props.data.Topics.map(function (topics) {
        var className = "bar-expand " + topics.name.toLowerCase();
        return (
          <div key={topics.name}>
            <br />
            <br />
            <h3>{topics.name}</h3>
            <p className="info">
              Skill level
              <em className="date">
                <b> </b>
              </em>
            </p>
            <li key={topics.name}>
              <span
                style={{ width: topics.progress1, opacity: 1 }}
                className={className}
                label={`${topics.ECTS} ECTS`}
              ></span>
              <br />
              <br />
              <span>
                {topics.sub1} <br />
                {topics.sub2} <br />
                {topics.sub3} <br />
                {topics.sub4} <br />
                {topics.sub5} <br />
                {topics.sub6} <br />
                {topics.sub7} <br />
                {topics.sub8} <br />
                <br />
              </span>
            </li>{" "}
            <br />
            <br />
            <br />
          </div>
        );
      });

      // Professional experience
      var profXp = this.props.data.ProfessionalExperience.map(function (
        profXp
      ) {
        var className = "bar-expand " + profXp.years.toLowerCase();
        return (
          <div key={profXp.years}>
            <br />
            <br />
            <h3>{profXp.years}</h3>
            <p className="info">of professional experience in</p>
            <li key={profXp.years}>
              <span>
                Technologies: <br />
                &bull; {profXp.language1} <br />
                &bull; {profXp.language2} <br />
                &bull; {profXp.language3} <br />
                &bull; {profXp.language4} <br />
                &bull; {profXp.language5} <br />
                &bull; {profXp.language6} <br />
                &bull; {profXp.language7} <br />
                &bull; {profXp.language8} <br />
                &bull; {profXp.language9} <br />
                &bull; {profXp.language10} <br />
                <br />
              </span>
            </li>{" "}
            <br />
            <br />
            <br />
          </div>
        );
      });
    }

    return (
      <section id="resume">
        <div className="row work">
          <div className="three columns header-col">
            <h1>
              <span>Work</span>
            </h1>
          </div>

          <div className="nine columns main-col">{work}</div>
        </div>

        <div className="row education">
          <div className="three columns header-col">
            <h1>
              <span>Education</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{education}</div>
            </div>
          </div>
        </div>

        <div className="row experience">
          <div className="three columns header-col">
            <h1>
              <span>Experience</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{profXp}</div>
            </div>
          </div>
        </div>

        <div className="row mobile">
          <div className="three columns header-col">
            <h1>
              <span>Mobile Apps</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="row item">
              <div className="twelve columns">{mobile}</div>
            </div>
          </div>
        </div>

        <div className="row topics">
          <div className="three columns header-col">
            <h1>
              <span>Educational Topics</span>
            </h1>
          </div>

          <div className="nine columns main-col">
            <div className="bars">
              <p>{topicmessage}</p>
              <ul className="topics">{topics}</ul>
              <ul className="skills">{skills}</ul>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Resume;
